<template>
  <attendance-statistics :title="$t(`attendance.${ type }`)" :result="result">
    <attendance-item v-for="(attendance, index) in attendances" :key="index" :attendance="attendance" @click.native="toClassAttendance(attendance)">
    </attendance-item>
  </attendance-statistics>
</template>

<script>
import { mapState } from 'vuex'
import { formatToYYYYMMDD } from '@/utils/format.js'
import attendanceStatistics from '@/views/attendance/AttendanceStatistics.vue'
import attendanceItem from '@/views/attendance/AttendanceItem.vue'

export default {
  name: 'attendance-grade',
  props: ['date'],
  components: {
    attendanceStatistics,
    attendanceItem
  },
  data () {
    return {
      result: {
        arrived: 0,
        total: 0
      },
      attendances: []
    }
  },
  computed: {
    ...mapState(['user']),
    type () {
      return this.$route.params.type
    },
    schoolLevelId () {
      return Number(this.$route.params.schoolLevelId)
    }
  },
  methods: {
    analysis (data) {
      let result = {
        arrived: data.arrivedStudentSchoolLevelTotal,
        total: data.studentSchoolLevelTotal
      }
      console.log(data)
      this.attendances = data.gradeStudentStat.map(val => {
        val.label = val.gradeLevel
        val.arrived = val.arrivedStudentGradeTotal
        val.total = val.studentGradeTotal
        val.precent = val.total > 0 ? val.arrived / val.total * 100 : 0
        val.proportion = val.total > 0 && result.total > 0 ? val.total / result.total * 100 : 0
        return val
      })
      this.result = result
    },
    toClassAttendance (attendance) {
      this.$router.push({ name: 'attendance-classes', params: { gradeId: attendance.idGrade } })
    },
    fetch () {
      this.$httpPost.force({
        token: this.user.token,
        idSchoolLevel: this.schoolLevelId,
        queryDate: this.date
      }, {
        cycle: formatToYYYYMMDD(new Date()) === this.date ? 60000 : 0
      })
    },
    reset () {
      this.result = {
        arrived: 0,
        total: 0
      }
      this.attendances = []
      this.fetch()
    }
  },
  watch: {
    date () {
      this.reset()
    }
  },
  created () {
    this.$httpPost = this.$http.createPost('admin/student/grade/attendance', {
      success: res => this.analysis(res.data)
    })
    this.fetch()
  },
  beforeDestroy () {
    if (this.$httpPost) {
      this.$httpPost.cancel()
    }
  }
}
</script>
