<template>
  <div class="l-navigation">
    <navigation class="l-navigation__header" :title="$t('home.menu.bracelet')"></navigation>
    <div class="l-navigation__content has-padding">
      <ul>
        <li v-for="figure in figures" :key="figure.id" class="c-bracelet-item">
          <div class="c-bracelet-item__avatar" :class="{ 'c-avatar': !figure.relationship, 'c-avatar--boy': figure.relationship === 1, 'c-avatar--girl': figure.relationship === 2 }">
            <div class="c-avatar__icon" :style="{ backgroundImage: `url(${ figure.headUrl }` }"></div>
          </div>
          <div class="c-bracelet-item__button o-button" :class="{ 't-adapt-button': figure.braceletOfBinding === 0, 'o-button--prominent': figure.braceletOfBinding === 1 }" @click="toggle(figure)">{{ figure.braceletOfBinding === 0 ? $t('bracelet.bind') : $t('bracelet.unbind') }}</div>
          <div class="c-bracelet-item__name">{{ figure.lastName || figure.userName }}</div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import navigation from '@/components/NavigationBar.vue'

export default {
  name: 'bracelet',
  components: {
    navigation
  },
  computed: {
    ...mapState(['figures']),
    isStaff () {
      return this.$route.params.mode === 'staff'
    }
  },
  methods: {
    toggle (figure) {
      if (figure.braceletOfBinding === 0) {
        this.$router.push({ name: 'scan', params: { id: this.isStaff ? figure.token : figure.studentId } })
      } else {
        this.$router.push({ name: 'unbind', params: { id: this.isStaff ? figure.token : figure.studentId } })
      }
    }
  }
}
</script>

<style lang="scss">
.c-bracelet-item {
  position: relative;
  padding: 35px 0 35px 80px;
  @include border-bottom-1px(#e6e6e6);
  &__avatar {
    position: absolute;
    top: 16px;
    left: 0;
    &.c-avatar {
      top: 12px;
    }
  }
  &__name {
    padding: 7px 0;
    margin-right: 90px;
    color: #404040;
    font-size: 16px;
    line-height: 1;
    white-space: nowrap;
    overflow: hidden;
  }
  &__button {
    float: right;
    width: 80px;
    padding: 7px 0;
  }
}
</style>
