<template>
  <div class="c-weekly-calendar">
    <div class="c-weekly-calendar__date">{{ date }}</div>
    <div class="c-weekly-calendar__wrapper">
      <div class="c-weekly-calendar__slide swiper-container" ref="swiper">
        <div class="swiper-wrapper">
          <div v-for="(block, index) in blocks" :key="index" class="c-weekly-calendar__block swiper-slide">
            <div v-for="(item, index) in block.items" :key="index" :id="item.date" class="c-weekly-calendar__item" @click="choose(item.date)">
              <div class="c-weekly-calendar__label" :class="{ 'is-active': selected === item.date, 'is-disabled': item.disabled }">{{ item.label }}</div>
            </div>
          </div>
        </div>
      </div>
      <div class="c-weekly-calendar__extend">
        <i class="c-weekly-calendar__icon"></i>
      </div>
    </div>
  </div>
</template>

<script>
import Swiper from 'swiper'

export default {
  name: 'calendar-weekly',
  props: ['defaultDate'],
  data () {
    return {
      startDate: null,
      endDate: null,
      lastDate: null,
      index: 1,
      date: '',
      blocks: [],
      selected: null
    }
  },
  computed: {},
  watch: {},
  methods: {
    fillZero (number) {
      if (number < 10) {
        return `0${ number }`
      }
      return number
    },
    format (date) {
      return `${ date.getFullYear() }-${ this.fillZero(date.getMonth() + 1) }-${ this.fillZero(date.getDate()) }`
    },
    addBlock () {
      let items = []
      let month = this.$t('format.year-month', {
        year: this.endDate.getFullYear(),
        month: this.$d(this.endDate, 'month')
      })
      let day
      for (let i = 0; i < 7; i++) {
        day = this.endDate.getDate()
        items[6 - i] = {
          label: day,
          date: this.$t('format.year-month-day', {
            year: this.endDate.getFullYear(),
            month: this.endDate.getMonth() + 1,
            day: day
          }),
          format: this.format(this.endDate)
        }
        this.endDate.setDate(day - 1)
      }
      this.blocks.unshift({
        items: items,
        date: month
      })
    },
    createBlock (date) {
      let items = []
      let day
      for (let i = 0; i < 7; i++) {
        day = date.getDate()
        items[i] = {
          label: day,
          date: this.format(date),
          disabled: date >= this.lastDate
        }
        date.setDate(day + 1)
      }
      return {
        items: items,
        date: this.$t('format.year-month', {
          year: date.getFullYear(),
          month: this.$d(date, 'month')
        })
      }
    },
    addPreBlock () {
      this.startDate.setDate(this.startDate.getDate() - 7)
      this.blocks.unshift(this.createBlock(this.cloneDate(this.startDate)))
      return true
    },
    addNextBlock () {
      if (this.endDate < this.lastDate) {
        this.blocks.push(this.createBlock(this.cloneDate(this.endDate)))
        this.endDate.setDate(this.endDate.getDate() + 7)
        return true
      }
      return false
    },
    refresh () {
      this.$nextTick(() => {
        this.$swiper.updateSlides()
        this.$swiper.slideTo(this.index, 0, false)
      })
    },
    choose (date) {
      if (this.selected !== date && new Date(date) < this.lastDate) {
        this.selected = date
        this.$emit('change', date)
      }
    },
    cloneDate (date) {
      return new Date(date.getTime())
    }
  },
  created () {
    if (this.defaultDate) {
      this.selected = this.defaultDate
    } else {
      this.choose(this.format(new Date()))
    }
    let today = new Date(this.format(new Date()))
    today.setDate(today.getDate() + 1)
    this.lastDate = today
    let date = new Date(this.selected)
    let week = date.getDay()
    if (week > 0) {
      date.setDate(date.getDate() - week)
    }
    this.blocks = [this.createBlock(this.cloneDate(date))]
    this.startDate = this.cloneDate(date)
    date.setDate(date.getDate() + 7)
    this.endDate = this.cloneDate(date)
    this.addPreBlock()
    this.addNextBlock()
  },
  mounted () {
    this.$swiper = new Swiper(this.$refs.swiper, {
      preventInteractionOnTransition: true,
      longSwipesRatio: 0.3,
      initialSlide: this.index,
      on: {
        slideChangeTransitionEnd: () => {
          this.$nextTick(() => {
            this.date = this.blocks[this.$swiper.activeIndex].date
            if (this.$swiper.activeIndex === 0) {
              this.addPreBlock()
              this.refresh()
            } else if (this.$swiper.activeIndex === this.blocks.length - 1) {
              if (this.addNextBlock()) {
                this.index += 1
                this.refresh()
              }
            } else {
              this.index = this.$swiper.activeIndex
            }
          })
        }
      }
    })
  },
  beforeDestroy () {
    if (this.$swiper) {
      this.$swiper.destroy()
    }
  }
}
</script>

<style lang="scss">
.c-weekly-calendar {
  flex: none;
  position: relative;
  height: 60px;
  &__date {
    padding-left: 15px;
    color: rgba(#333, 0.3);
    font-size: 10px;
    line-height: 18px;
  }
  &__wrapper {
    display: flex;
  }
  &__slide {
    flex: auto;
  }
  &__block {
    display: flex;
    width: 100%;
    height: 40px;
  }
  &__item {
    flex: 1;
    text-align: center;
  }
  &__label {
    display: inline-block;
    width: 25px;
    height: 25px;
    color: #333;
    font-size: 12px;
    line-height: 25px;
    border-radius: 50%;
    &.is-active {
      color: #fff;
      background: linear-gradient(90deg, #4ca3f9, #4976f2);
    }
    &.is-disabled {
      opacity: 0.5;
    }
  }
  &__extend {
    flex: none;
    width: 60px;
    text-align: center;
    & > i {
      display: inline-block;
      @include bg-img-ratio('./calendar', 41, 43);
    }
  }
}
</style>
