<template>
  <div class="l-form c-modify">
    <form-field type="number" :placeholder="$t('mobile.newPhoneNumber')" v-model.trim="mobile">
      <area-code @change="pick"></area-code>
    </form-field>
    <div class="o-button o-button--submit t-adapt-button--active" :class="{ 'is-active': isValid }" @click="save">{{ $t('common.save') }}</div>
    <div class="c-modify__error">{{ error }}</div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import formField from '@/components/FormField.vue'
import areaCode from '@/widgets/AreaCode.vue'

export default {
  name: 'modify-mobile',
  components: {
    formField,
    areaCode
  },
  data () {
    return {
      mobile: '',
      areaCode: '',
      error: ''
    }
  },
  computed: {
    ...mapState(['user']),
    isValid () {
      return this.mobile && this.areaCode
    }
  },
  methods: {
    pick (areaCode) {
      this.areaCode = areaCode
    },
    save () {
      if (this.isValid) {
        this.error = ''
        if (!this.$httpPost) {
          this.$httpPost = this.$http.createPost(this.$route.params.mode === 'parent' ? 'parent/mobile/modify' : 'staff/mobile/modify', {
            opts: {
              lock: true
            },
            success: (res, data) => {
              this.user.mobile = data.mobile
              this.$router.back()
            },
            fail: error => {
              this.error = error.message
            }
          })
        }
        this.$httpPost.request({
          token: this.$route.params.token,
          mobile: this.mobile
        })
      }
    }
  },
  beforeDestroy () {
    if (this.$httpPost) {
      this.$httpPost.cancel()
    }
  }
}
</script>

<style lang="scss" scoped>
