<template>
  <div class="l-navigation">
    <navigation class="l-navigation__header" :title="$t('setting.language')">
      <div class="c-navigation-bar__button t-adapt-text" @click="save">{{ $t('common.save') }}</div>
    </navigation>
    <div class="l-navigation__content has-padding">
      <ul>
        <li v-for="(lang, i) in langs" :key="lang.key" class="c-setting-item" @click="change(i)">
          {{ lang.text }}
          <div class="c-setting-item__extend" :class="{ 'o-radio': index === i }"></div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import { langs } from '@/locale/index.js'
import navigation from '@/components/NavigationBar.vue'

export default {
  name: '',
  components: {
    navigation
  },
  data () {
    return {
      index: 0,
      defaultIndex: 0
    }
  },
  computed: {
    langs () {
      return langs
    }
  },
  methods: {
    change (index) {
      this.index = index
    },
    save () {
      if (this.index !== this.defaultIndex) {
        this.$i18n.locale = this.langs[this.index].key
        window.localStorage.setItem('locale', this.$i18n.locale)
      }
      this.$router.back()
    }
  },
  created () {
    let locale = this.$i18n.locale
    this.index = this.langs.findIndex(lang => lang.key === locale)
    this.defaultIndex = this.index
  }
}
</script>
