<template>
  <div class="l-navigation p-account">
    <navigation class="l-navigation__header" :title="$t('home.menu.account')"></navigation>
    <div class="l-navigation__content has-padding">
      <ul>
        <li class='c-account-info'>
          <div class="c-account-info__name u-more">{{ user.lastName || user.userName }}</div>
          <div class="c-account-info__avatar c-avatar" @click="upload">
            <div class="c-avatar__icon" :style="{ backgroundImage: `url(${ user.headUrl }` }"></div>
          </div>
        </li>
        <li v-if="isStaff" class="c-account-item">
          <div class="c-account-item__label">{{ $t('account.id') }}</div>
          <div class="c-account-item__value">{{ user.legacyId }}</div>
        </li>
        <li v-if="!isStaff" class="c-account-item">
          <div class="c-account-item__label">{{ $t('account.relationship') }}</div>
          <div class="c-account-item__value">{{ user.relationship || '&nbsp;' }}</div>
        </li>
        <router-link tag="li" class="c-account-item" :to="{ name: 'modify-mobile-confirm' }">
          <div class="c-account-item__label">{{ $t('account.phone') }}</div>
          <div class="c-account-item__value u-more">{{ user.mobile  || '&nbsp;' }}</div>
        </router-link>
        <router-link tag="li" class="c-account-item" :to="{ name: 'modify-email' }">
          <div class="c-account-item__label">{{ $t('account.email') }}</div>
          <div class="c-account-item__value u-more">{{ user.email || '&nbsp;' }}</div>
        </router-link>
      </ul>
      <input class="u-display-none" ref="fileUpload" type="file" accept="image/*" @change="handleFile" />
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import navigation from '@/components/NavigationBar.vue'

export default {
  name: 'account-info',
  components: {
    navigation
  },
  data () {
    return {
      cancelSource: null
    }
  },
  computed: {
    ...mapState(['user']),
    isStaff () {
      return this.$route.params.mode === 'staff'
    },
    url () {
      return this.isStaff ? 'staff/pic/modify' : ''
    }
  },
  methods: {
    upload () {
      if (this.user && this.url && this.$refs.fileUpload && !this.cancelSource) {
        this.$refs.fileUpload.click()
      }
    },
    handleFile (e) {
      let target = e.target || e.srcElement
      let file = target.files[0]
      if (!file) {
        return
      }
      this.cancelSource = this.$http.cancelSource()
      this.$http.uploadFile('common/attachment/upload', file, {
        cancelToken: this.cancelSource.token
      }).then(res => {
        this.cancelSource = null
        this.changeAvatar(res.data.realPath)
      }, () => {
        this.cancelSource = null
      })
    },
    changeAvatar (path) {
      this.cancelSource = this.$http.cancelSource()
      this.$http.post(this.url, {
        token: this.user.token,
        picImgPath: path
      }, {
        cancelToken: this.cancelSource.token
      }).then(() => {
        this.cancelSource = null
        this.user.headUrl = path
      }, () => {
        this.cancelSource = null
      })
    }
  },
  beforeDestroy () {
    if (this.cancelSource) {
      this.cancelSource.cancel('destroy')
    }
  }
}
</script>
