<template>
  <div class="l-navigation attendance-wrapper c-sign-in-information">
    <navigation class="l-navigation__header" :title="$t('attendance.title')"></navigation>
    <div class="l-navigation__content">
      <div class="c-sign-in-information__wrapper">
        <character-intro class="c-sign-in-information__intro c-character-intro--black" :person="student" :smaller="false">
          <div class="c-sign-in-information__phone_wrapper">
            <div class="c-sign-in-information__phone"></div>
          </div>
        </character-intro>
        <div v-if="manual" class="c-sign-in-information__operation c-sign-in">
          <div class="c-sign-in__item">{{ $t('signInType.2') }}<i class="c-sign-in__icon" :class="{ 'c-sign-in__icon--checked': signInType === 2 }" @click="signIn(2)"></i></div>
          <div class="c-sign-in__item">{{ $t('signInType.3') }}<i class="c-sign-in__icon" :class="{ 'c-sign-in__icon--checked': signInType === 3 }" @click="signIn(3)"></i></div>
        </div>
        <div class="c-sign-in-information__data">
          <div class="c-sign-in-data" @click="toHistory('normal')">
            <div class="c-sign-in-data__count">{{ normal }}</div>
            <div v-once class="c-sign-in-data__label">{{ $t('signIn.normal') }}</div>
          </div>
          <div class="c-sign-in-data" @click="toHistory('abnormal')">
            <div class="c-sign-in-data__count c-sign-in-data__count--prominent">{{ abnormal }}</div>
            <div v-once class="c-sign-in-data__label">{{ $t('signIn.abnormal') }}</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import navigation from '@/components/NavigationBar.vue'
import characterIntro from '@/views/shared/CharacterIntro.vue'

export default {
  name: 'sign-in-infomation',
  components: {
    navigation,
    characterIntro
  },
  data () {
    return {
      student: null,
      normal: 0,
      abnormal: 0,
      signInType: 0, // 0：未提交 1：正常 2：迟到（手工签到） 3：请假
      submitType: 0
    }
  },
  computed: {
    ...mapState(['user']),
    studentId () {
      return Number(this.$route.params.studentId)
    },
    manual () {
      return this.student && this.$route.params.type === 'manual'
    }
  },
  methods: {
    analysis (data) {
      this.student = {
        id: data.idStudent,
        name: data.lastName,
        headUrl: data.headUrl,
        info: data.parentEmail,
        tag: this.$t('format.classInfo2', {
          gradeLevel: data.gradeLevel,
          classId: data.classId
        })
      }
      this.normal = data.accReachCount
      this.abnormal = data.accAbsenceCount
    },
    signIn (type) {
      if (this.submitType === type) {
        return
      }
      if (!this.$siginInHttpPost) {
        this.$siginInHttpPost = this.$http.createPost('teacher/student/signin', {
          opts: {
            lock: true
          },
          success: () => {
            this.signInType = this.submitType
          },
          fail: () => {
            this.submitType = 0
          }
        })
      }
      this.submitType = type
      this.$siginInHttpPost.force({
        token: this.user.token,
        idStudent: this.studentId,
        type: type
      })
    },
    toHistory (type) {
      if (this.student) {
        this.$router.push({ name: 'attendance-history', params: { studentId: this.student.id, type } })
      }
    }
  },
  created () {
    this.$httpPost = this.$http.createPost('teacher/studentstat', {
      opts: {
        cycle: 60000
      },
      success: res => this.analysis(res.data)
    })
    this.$httpPost.request({
      token: this.user.token,
      idStudent: this.studentId
    })
  },
  beforeDestroy () {
    this.$httpPost.cancel()
    if (this.$siginInHttpPost) {
      this.$siginInHttpPost.cancel()
    }
  }
}
</script>

