<template>
  <div class="l-navigation p-setting">
    <navigation class="l-navigation__header" :title="$t('home.menu.setting')"></navigation>
    <div class="l-navigation__content has-padding">
      <ul>
        <li class="c-setting-item">
          {{ $t('setting.voice') }}
          <div class="c-setting-item__extend o-switch" :class="{ 'is-on': voice }" @click="toggleVoice"></div>
        </li>
        <li class="c-setting-item">
          {{ $t('setting.notice') }}
          <div class="c-setting-item__extend o-switch" :class="{ 'is-on': notice }" @click="toggleNotice"></div>
        </li>
        <router-link tag="li" class="c-setting-item" :to="{ name: 'modify-language' }">
          {{ $t('setting.language') }}
          <div class="c-setting-item__value u-more">{{ langVal }}</div>
        </router-link>
      </ul>
    </div>
  </div>
</template>

<script>
import i18n, { langs } from '@/locale/index.js'
import navigation from '@/components/NavigationBar.vue'

export default {
  name: 'setting',
  components: {
    navigation
  },
  data () {
    return {
      voice: false,
      notice: false,
      lang: null
    }
  },
  computed: {
    langVal () {
      return this.lang ? this.lang.text : ''
    }
  },
  methods: {
    toggleVoice () {
      this.voice = !this.voice
    },
    toggleNotice () {
      this.notice = !this.notice
    }
  },
  created () {
    this.lang = langs.find(lang => lang.key === i18n.locale)
  }
}
</script>
