<template>
  <div class="l-navigation p-attendance-history">
    <navigation class="l-navigation__header" :title="$t('attendance.history')"></navigation>
    <div class="c-attendance-type">
      <router-link class="o-tab c-attendance-type__tab" :class="{ 'is-active': currentType === 'normal' }" :to="{ params: { type: 'normal' } }" replace>{{ $t('signIn.normal') }}</router-link>
      <router-link class="o-tab c-attendance-type__tab" :class="{ 'is-active': currentType === 'abnormal' }" :to="{ params: { type: 'abnormal' } }" replace>{{ $t('signIn.abnormal') }}</router-link>
    </div>
    <div class="l-navigation__content u-overflow-hidden" ref="wrapper">
      <div>
        <ul ref="listWrapper">
          <li v-for="(attendanceSet, index) in attendances" :key="index">
            <div v-once class='p-attendance-history__time'>{{ attendanceSet.label }}</div>
            <div class='p-attendance-history__list'>
              <div v-for="(item, index) in attendanceSet.attendances" :key="index" class="c-attendance-history-item">
                <div class="c-attendance-history-item__info u-clearfix">
                  <div class="c-attendance-history-item__date">{{ item.date }}</div>
                  <div class="c-attendance-history-item__state">{{ $t(`signInType.${ item.type }`) }}</div>
                </div>
                <div class="c-attendance-history-item__time">{{ item.time }}</div>
              </div>
            </div>
          </li>
        </ul>
        <div v-if="canPullUp" class="p-sign-in-history__pullup">{{ pullUpTip }}</div>
      </div>
      <loading :fetching="loading" :isError="isError" :retry="fetch"></loading>
      <div v-if="empty" class='p-attendance-history__time'>{{ $t('common.empty') }}</div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { getRect } from '@/utils/dom.js'
import navigation from '@/components/NavigationBar.vue'
import loading from '@/components/Loading.vue'
import BScroll from 'better-scroll'

export default {
  name: 'attendance-history',
  components: {
    navigation,
    loading
  },
  data () {
    return {
      httpPost: null,
      page: 1,
      isPullUpLoad: false,
      error: false,
      hasMore: true,
      hasFetched: false,
      attendances: null,
      currentType: null,
      $normalAttendances: [],
      $abnormalAttendances: []
    }
  },
  computed: {
    ...mapState(['user']),
    studentId () {
      return Number(this.$route.params.studentId)
    },
    fetching () {
      return this.httpPost && this.httpPost.isRequesting()
    },
    loading () {
      return this.fetching && !this.hasFetched
    },
    isError () {
      return this.error && !this.hasFetched
    },
    has () {
      return this.attendances.length > 0
    },
    empty () {
      return this.hasFetched && !this.has && !this.hasMore
    },
    canPullUp () {
      return this.hasFetched && this.hasMore
    },
    pullUpTip () {
      return this.isPullUpLoad ? this.$t('pullUp.loading') : this.hasMore ? this.$t('pullUp.more') : this.$t('pullUp.finished')
    }
  },
  methods: {
    updateAttendances (type) {
      if (this.currentType !== type) {
        if (type === 'abnormal') {
          this.attendances = this.$data.$abnormalAttendances
        } else {
          this.attendances = this.$data.$normalAttendances
        }
        this.currentType = type
        this.forceUpdate(true)
      }
    },
    initScroll () {
      if (!this.$refs.wrapper || !this.hasFetched || this.$scroll) {
        return
      }
      this.$refs.listWrapper.style.minHeight = `${ getRect(this.$refs.wrapper).height + 1 }px`
      this.$scroll = new BScroll(this.$refs.wrapper, {
        scrollY: true,
        probeType: 2,
        pullUpLoad: {
          threshold: 10
        }
      })
      this.$scroll.on('pullingUp', () => {
        if (this.hasMore && !this.fetching) {
          this.isPullUpLoad = true
          this.fetch()
        } else {
          this.$scroll.finishPullUp()
        }
      })
    },
    pullUpFnished () {
      if (this.isPullUpLoad) {
        this.isPullUpLoad = false
        this.$scroll.finishPullUp()
      }
    },
    forceUpdate (toTop) {
      this.$nextTick(() => {
        if (this.$scroll) {
          this.pullUpFnished()
          this.$scroll.refresh()
          if (toTop) {
            this.$scroll.scrollTo(0, 0, 0)
          }
        } else {
          this.initScroll()
        }
      })
    },
    isSameMonth (date, date2) {
      return date.getFullYear() === date2.getFullYear() && date.getMonth() === date2.getMonth()
    },
    isSameDay (date, date2) {
      return this.isSameMonth(date, date2) && date.getDate() === date2.getDate()
    },
    checkDateStr (date) {
      let today = new Date()
      if (this.isSameDay(today, date)) {
        return this.$t('history.today')
      } else {
        today.setDate(today.getDate() - 1)
        if (this.isSameDay(today, date)) {
          return this.$t('history.yesterday')
        }
      }
      return this.$t('format.month-day', {
        month: this.$d(date, 'month'),
        day: date.getDate()
      })
    },
    addData (attendance) {
      let attendances
      if (attendance.type === 3) {
        attendances = this.$data.$abnormalAttendances
      } else {
        attendances = this.$data.$normalAttendances
      }
      let info = attendances[attendances.length - 1]
      let date = new Date(attendance.checkTime)
      if (!info || !this.isSameMonth(info.date, date)) {
        info = {
          label: this.$t('format.year-month', {
            year: date.getFullYear(),
            month: this.$d(date, 'month')
          }),
          date: date,
          attendances: []
        }
        attendances.push(info)
      }
      info.attendances.push({
        date: this.checkDateStr(date),
        time: attendance.checkTimeStr.split(' ')[1],
        type: attendance.type
      })
    },
    add (attendances) {
      this.hasFetched = true
      if (attendances.length === 0) {
        this.hasMore = false
        return
      }
      for (let i = 0, j = attendances.length; i < j; i++) {
        this.addData(attendances[i])
      }
      this.page += 1
    },
    fetch () {
      this.httpPost.request({
        token: this.user.token,
        idStudent: this.studentId,
        page: this.page,
        pageSize: 20
      })
    }
  },
  watch: {
    fetching (nVal, oVal) {
      if (oVal && !nVal) {
        setTimeout(() => {
          this.forceUpdate()
        }, 20)
      }
    }
  },
  beforeRouteUpdate (to, from, next) {
    this.updateAttendances(to.params.type)
    next()
  },
  created () {
    this.updateAttendances(this.$route.params.type)
    this.httpPost = this.$http.createPost('teacher/student/signinrecord', {
      success: (res) => {
        this.error = false
        this.add(res.data)
      },
      fail: () => {
        this.error = true
      }
    })
    this.fetch()
  },
  mounted () {
    this.initScroll()
  },
  beforeDestroy () {
    if (this.httpPost) {
      this.httpPost.cancel()
    }
    if (this.$scroll) {
      this.$scroll.destroy()
    }
  }
}
</script>

<style lang="scss" scoped>
.p-attendance-history {
  &__time {
    padding: 18px 15px 10px;
    color: rgba(#333, 0.4);
    font-size: 14px;
    line-height: 1;
    background-color: #f2f2f2;
  }
  &__list {
    padding: 15px;
    background-color: #fff;
  }
}
.c-attendance-history-item {
  padding: 15px;
  background-color: #fff;
  &__info {
    color: #333;
    font-size: 14px;
  }
  &__date {
    float: left;
  }
  &__state {
    float: right;
  }
  &__time {
    margin-top: 6px;
    text-align: right;
    color: rgba(#333, 0.7);
    font-size: 12px;
    line-height: 1;
  }
}
.c-attendance-type {
  padding-top: 6px;
  text-align: center;
  &__tab {
    min-width: 120px;
    & + & {
      margin-left: 40px;
    }
  }
}
</style>
