<template>
  <div class="l-form c-modify">
    <form-field :label="$t('setPwd.oldPwd')" v-model="oldPassword">
    </form-field>
    <form-field :label="$t('setPwd.newPwd')" :tip="$t('setPwd.tip')" :error="passwordErr" v-model="password">
    </form-field>
    <form-field :label="$t('common.confirm')" :error="confirmPasswordErr" v-model="confirmPassword">
    </form-field>
    <div class="o-button o-button--submit t-adapt-button--active c-modify__submit" :class="{ 'is-active': isValid }" @click="save">{{ $t('common.save') }}</div>
    <div class="c-modify__error">{{ error }}</div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import formField from '@/components/FormField.vue'

export default {
  name: 'modify-pwd',
  components: {
    formField
  },
  data () {
    return {
      oldPassword: '',
      password: '',
      confirmPassword: '',
      passwordErr: '',
      confirmPasswordErr: '',
      error: '',
      cancelSource: null
    }
  },
  computed: {
    ...mapState(['user']),
    isStaff () {
      return this.$route.params.mode === 'staff'
    },
    isValid () {
      return this.oldPassword && this.password && this.confirmPassword
    }
  },
  methods: {
    save () {
      if (!this.isValid) {
        return
      }
      this.error = ''
      if (this.password.length < 8) {
        this.passwordErr = this.$t('pwdErr.tooShort')
        this.confirmPasswordErr = ''
        return
      } else if (this.password.length > 16) {
        this.passwordErr = this.$t('pwdErr.tooLong')
        this.confirmPasswordErr = ''
        return
      } else if (!/(?=.*[a-z])(?=.*\d)(?=.*[#@!?~%^&*])[a-z\d#@!?~%^&*]/.test(this.password)) {
        this.passwordErr = this.$t('pwdErr.format')
        this.confirmPasswordErr = ''
        return
      } else {
        this.passwordErr = ''
      }
      if (this.confirmPassword !== this.password) {
        this.confirmPasswordErr = this.$t('pwdErr.notMatch')
        return
      } else {
        this.confirmPasswordErr = ''
      }
      if (!this.$httpPost) {
        this.$httpPost = this.$http.createPost(this.isStaff ? 'staff/password/modify' : 'parent/password/modify', {
          opts: {
            lock: true
          },
          success: () => {
            this.$router.back()
          },
          fail: error => {
            this.error = error.message
          }
        })
      }
      this.$httpPost.request({
        token: this.user.token,
        oldPwd: this.oldPassword,
        newPwd: this.password
      })
    }
  },
  beforeDestroy () {
    if (this.$httpPost) {
      this.$httpPost.cancel()
    }
  }
}
</script>
