<template>
  <div class="l-navigation">
    <navigation class="l-navigation__header" :title="$t('home.menu.kids')"></navigation>
    <div class="l-navigation__content has-padding">
      <ul>
        <li class='c-account-info'>
          <div class="c-account-info__name u-more">{{ name || '&nbsp;' }}</div>
          <div v-if="figure" class="c-account-info__avatar" :class="{ 'c-avatar--boy': sex === 1, 'c-avatar--girl': sex === 2 }" @click="upload">
            <div class="c-avatar__icon" :style="{ backgroundImage: `url(${ figure.headUrl }` }"></div>
          </div>
        </li>
        <li class="c-account-item">
          <div class="c-account-item__label">{{ $t('account.relationship') }}</div>
          <div class="c-account-item__value">{{ relationship || '&nbsp;' }}</div>
        </li>
        <li class="c-account-item">
          <div class="c-account-item__label">{{ $t('account.student') }}</div>
          <div class="c-account-item__value">{{ studentId || '&nbsp;' }}</div>
        </li>
        <li class="c-account-item" @click="changeBus">
          <div class="c-account-item__label">{{ $t('account.bus') }}</div>
          <div class="c-account-item__value u-more">{{ bus || '&nbsp;' }}</div>
        </li>
      </ul>
      <input class="u-display-none" ref="fileUpload" type="file" accept="image/*" @change="handleFile" />
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import navigation from '@/components/NavigationBar.vue'

export default {
  name: 'children-info',
  components: {
    navigation
  },
  data () {
    return {
      cancelSource: null
    }
  },
  computed: {
    ...mapState(['user']),
    id () {
      return this.$route.params.id
    },
    figure () {
      return this.$store.getters.getFigure({
        mode: 'parent',
        id: this.id
      })
    },
    name () {
      return this.figure ? this.figure.lastName : null
    },
    sex () {
      return this.figure ? this.figure.relationship : 1
    },
    relationship () {
      return this.figure ? this.figure.relationship === 1 ? this.$t('relationship.son') : this.$t('relationship.daughter') : null
    },
    studentId () {
      return this.figure ? this.figure.studentId : null
    },
    bus () {
      return this.figure ? this.figure.busId : null
    }
  },
  methods: {
    upload () {
      if (this.figure && this.$refs.fileUpload && !this.cancelSource) {
        this.$refs.fileUpload.click()
      }
    },
    handleFile (e) {
      let target = e.target || e.srcElement
      let file = target.files[0]
      if (!file) {
        return
      }
      this.cancelSource = this.$http.cancelSource()
      this.$http.uploadFile('common/attachment/upload', file, {
        cancelToken: this.cancelSource.token
      }).then(res => {
        this.cancelSource = null
        this.changeAvatar(res.data.realPath)
      }, () => {
        this.cancelSource = null
      })
    },
    changeAvatar (path) {
      this.cancelSource = this.$http.cancelSource()
      this.$http.post('parent/child/modifyPic', {
        token: this.user.token,
        idStudent: this.id,
        picImgPath: path
      }, {
        cancelToken: this.cancelSource.token
      }).then(() => {
        this.cancelSource = null
        this.figure.headUrl = path
      }, () => {
        this.cancelSource = null
      })
    },
    changeBus () {
      if (this.figure) {
        this.$router.push({ name: 'modify-bus', params: { id: this.figure.studentId } })
      }
    }
  },
  beforeDestroy () {
    if (this.cancelSource) {
      this.cancelSource.cancel('destroy')
    }
  }
}
</script>
