<template>
  <div class="l-navigation p-attendance-wrapper">
    <navigation class="l-navigation__header" :title="$t('attendance.title')"></navigation>
    <weekly-calendar @change="change" :default-date="date"></weekly-calendar>
    <router-view class="l-navigation__content" :date="date" />
  </div>
</template>

<script>
import { formatToYYYYMMDD } from '@/utils/format.js'
import navigation from '@/components/NavigationBar.vue'
import weeklyCalendar from '@/components/CalendarWeekly.vue'

export default {
  name: 'attendance-wrapper',
  components: {
    navigation,
    weeklyCalendar
  },
  computed: {
    date () {
      return this.$route.params.date || formatToYYYYMMDD(new Date())
    }
  },
  methods: {
    change (date) {
      if (this.date !== date) {
        this.$router.replace({ params: { date } })
      }
    }
  }
}
</script>
