<template>
  <div class="l-operation p-unbind">
    <div class="l-operation__title">{{ $t('unbind.title') }}</div>
    <div class="l-operation__header">{{ $t('unbind.tip') }}</div>
    <div class="l-operation__content">
      <div class="p-unbind__img">
        <div class="p-unbind__avatar" :class="{ 'c-solid-avatar': sex === 0, 'c-solid-avatar--boy': sex === 1, 'c-solid-avatar--girl': sex === 2 }">
          <div v-if="figure" class="c-solid-avatar__icon" :style="{ backgroundImage: `url(${ figure.headUrl }` }"></div>
        </div>
        <div class="p-unbind__link"></div>
      </div>
    </div>
    <div class="l-operation__footer">
      <div class="l-operation__button o-border-button o-button--cancel" @click="cancel">{{ $t('common.cancel') }}</div>
      <div class="l-operation__button o-button o-button--prominent" @click="unbind">{{ fail ? $t('bracelet.retry') : $t('bracelet.unbind') }}</div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'

export default {
  name: 'unbind',
  computed: {
    ...mapState(['user']),
    isStaff () {
      return this.$route.params.mode === 'staff'
    },
    url () {
      return this.isStaff ? 'staff/bracelet/unbind' : 'parent/bracelet/unbind'
    },
    id () {
      return this.$route.params.id
    },
    figure () {
      return this.$store.getters.getFigure(this.$route.params)
    },
    sex () {
      return this.figure && this.figure.relationship || 0
    }
  },
  methods: {
    cancel () {
      this.$router.back()
    },
    unbind () {
      if (!this.$httpPost) {
        this.$httpPost = this.$http.createPost(this.url, {
          opts: {
            lock: true
          },
          success: () => {
            if (this.figure) {
              this.figure.braceletOfBinding = 0
            }
            this.cancel()
          }
        })
      }
      this.$httpPost.request(this.isStaff ? {
        token: this.user.token
      } : {
        idStudent: this.id
      })
    }
  },
  beforeDestroy () {
    if (this.$httpPost) {
      this.$httpPost.cancel()
    }
  }
}
</script>

