<template>
  <div class="p-pre-modify-mobile is-form c-modify">
    <div class="p-pre-modify-mobile__nav">
      <div class="o-fixed-tab" :class="{ 'is-active': mode === 0 }" @click="changeMode(0)">{{ $t('mobile.tab.phone') }}</div>
      <div class="o-fixed-tab" :class="{ 'is-active': mode === 1 }" @click="changeMode(1)">{{ $t('mobile.tab.email') }}</div>
    </div>
    <form-field v-if="mode === 0" type="number" class="c-form-field--select" :label="$t('mobile.oldPhoneNumber')" v-model="mobile">
      <area-code @change="pick"></area-code>
    </form-field>
    <form-field v-else class="c-form-field" :label="$t('mobile.oldEmail')" :placeholder="$t('mobile.oldEmail')" v-model="email">
    </form-field>
    <form-field class="c-form-field--button" :label="$t('sms.vCode')" v-model="vCode">
      <sms :mobile="mobile" :areaCode="areaCode" :email="email" :mode="mode"></sms>
    </form-field>
    <div class="o-button o-button--submit t-adapt-button--active c-modify__submit" :class="{ 'is-active': isValid }" @click="next">{{ $t('common.next') }}</div>
    <div class="c-modify__error">{{ error }}</div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import formField from '@/components/FormField.vue'
import areaCode from '@/widgets/AreaCode.vue'
import sms from '@/widgets/Sms.vue'

export default {
  name: 'pre-modify-mobile',
  components: {
    formField,
    areaCode,
    sms
  },
  data () {
    return {
      mode: 0,
      areaCode: '',
      mobile: '',
      email: '',
      vCode: '',
      error: ''
    }
  },
  computed: {
    ...mapState(['user']),
    isStaff () {
      return this.$route.params.mode === 'staff'
    },
    mobileVal () {
      return this.areaCode && this.mobile ? this.areaCode + this.mobile : null
    },
    account () {
      return this.mode === 0 ? this.mobileVal : this.email
    },
    isValid () {
      return this.account && this.vCode
    },
    url () {
      switch (this.mode) {
        case 0:
          return this.isStaff ? 'staff/mobile/checkvcode' : 'staff/mobile/checkvcode'
        case 1:
          return this.isStaff ? 'staff/email/checkvcode' : 'staff/email/checkvcode'
        default:
          return ''
      }
    }
  },
  methods: {
    changeMode (mode) {
      if (this.mode !== mode) {
        this.areaCode = ''
        this.mobile = ''
        this.email = ''
        this.vCode = ''
        this.mode = mode
        if (this.$httpPost) {
          this.$httpPost.setUrl(this.url)
        }
      }
    },
    pick (areaCode) {
      this.areaCode = areaCode
    },
    next () {
      if (this.isValid) {
        this.error = ''
        if (!this.$httpPost) {
          this.$httpPost = this.$http.createPost(this.url, {
            opts: {
              lock: true
            },
            success: () => {
              this.$router.replace({ name: 'modify-mobile' })
            },
            fail: error => {
              this.error = error.message
            }
          })
        }
        this.$httpPost.request(this.mode === 0 ? {
          token: this.user.token,
          mobile: this.mobile,
          vCode: this.vCode
        } : {
          token: this.user.token,
          email: this.email,
          vCode: this.vCode
        })
      }
    }
  },
  beforeDestroy () {
    if (this.$httpPost) {
      this.$httpPost.cancel()
    }
  }
}
</script>

<style lang="scss">
.p-pre-modify-mobile {
  &__nav {
    margin-bottom: 40px;
    text-align: center;
    .o-fixed-tab + .o-fixed-tab {
      margin-left: 115px;
    }
  }
}
</style>
