<template>
  <div class="l-form c-modify">
    <div class="l-form__title">{{ $t('resetPwd.title') }}</div>
    <form-field class="c-form-field--select" :label="$t('sms.phone')" v-model="mobile">
      <area-code @change="pick"></area-code>
    </form-field>
    <form-field class="c-form-field--button" :label="$t('sms.vCode')" v-model="vCode">
      <sms :mobile="mobile" :areaCode="areaCode"></sms>
    </form-field>
    <div class="o-button o-button--submit t-adapt-button--active c-modify__submit" :class="{ 'is-active': isValid }" @click="next">{{ $t('common.next') }}</div>
    <div class="c-modify__error">{{ error }}</div>
  </div>
</template>

<script>
import formField from '@/components/FormField.vue'
import areaCode from '@/widgets/AreaCode.vue'
import sms from '@/widgets/Sms.vue'

export default {
  name: 'pre-set-pwd',
  components: {
    formField,
    areaCode,
    sms
  },
  data () {
    return {
      areaCode: '',
      mobile: '',
      vCode: '',
      error: ''
    }
  },
  computed: {
    isValid () {
      return this.areaCode && this.mobile && this.vCode
    }
  },
  methods: {
    next () {
      if (this.isValid) {
        this.error = ''
        if (!this.$httpPost) {
          this.$httpPost = this.$http.createPost('staff/preModify', {
            data: {
              mobile: this.areaCode + this.mobile,
              vCode: this.vCode
            },
            opts: {
              lock: true
            },
            success: res => {
              this.$router.replace({ name: this.$route.params.next || 'reset-pwd', params: { mode: this.$route.params.mode, token: res.data.token } })
            },
            fail: error => {
              this.error = error.message
            }
          })
        }
        this.$httpPost.request()
      }
    },
    pick (areaCode) {
      this.areaCode = areaCode
    }
  },
  beforeDestroy () {
    if (this.$httpPost) {
      this.$httpPost.cancel()
    }
  }
}
</script>
