<template>
  <div class="l-navigation c-modify">
    <navigation class="l-navigation__header" :title="$t('account.email')"></navigation>
    <div class="l-navigation__content l-form">
      <form-field :placeholder="$t('email.placeholder')" v-model.trim="email">
      </form-field>
      <div class="o-button o-button--submit t-adapt-button--active c-modify__submit" :class="{ 'is-active': isValid }" @click="save">{{ $t('common.save') }}</div>
      <div class="c-modify__error">{{ error }}</div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import navigation from '@/components/NavigationBar.vue'
import formField from '@/components/FormField.vue'

export default {
  name: 'modify-email',
  components: {
    navigation,
    formField
  },
  data () {
    return {
      email: '',
      error: ''
    }
  },
  computed: {
    ...mapState(['user']),
    isStaff () {
      return this.$route.params.mode === 'staff'
    },
    isValid () {
      return !!this.email
    }
  },
  methods: {
    save () {
      if (this.isValid) {
        this.error = ''
        if (!this.$httpPost) {
          this.$httpPost = this.$http.createPost(this.isStaff ? 'staff/emali/modify' : 'parent/emali/modify', {
            opts: {
              lock: true
            },
            success: (res, data) => {
              this.user.emali = data.emali
              this.$router.back()
            },
            fail: error => {
              this.error = error.message
            }
          })
        }
        this.$httpPost.request({
          token: this.user.token,
          emali: this.emali
        })
      }
    }
  },
  beforeDestroy () {
    if (this.$httpPost) {
      this.$httpPost.cancel()
    }
  }
}
</script>

