        <router-link tag="li" v-if="!isStaff" class="c-side-menu__link" :to="{ name: 'children' }"><i class="c-side-menu__icon--kids"></i>{{ $t('home.menu.kids') }}</router-link>
        <router-link tag="li" class="c-side-menu__link" :to="{ name: 'pre-modify-pwd', params: { next: 'modify' } }"><i class="c-side-menu__icon--password"></i>{{ $t('home.menu.password') }}</router-link>
        <router-link tag="li" class="c-side-menu__link" :to="{ name: 'setting' }"><i class="c-side-menu__icon--setting"></i>{{ $t('home.menu.setting') }}</router-link>
