      </ul>
      <div class="c-side-menu__button o-button o-button--unwish" @click="logout">{{ $t('home.menu.logout') }}</div>
    </div>
  </div>
</template>

<script>
import * as Types from '@/store/mutation-types.js'

export default {
  name: 'side-menu',
  props: ['user'],
  computed: {
    isStaff () {
      return this.$route.params.mode === 'staff'
    }
  },
  methods: {
    logout () {
      this.$store.dispatch(Types.LOGOUT)
      this.$router.replace({ name: 'login' })
    }
  }
}
</script>

