<template>
  <div class="l-operation p-bind-failure">
    <div class="l-operation__title">{{ $t('bind.title') }}</div>
    <div class="p-bind-failure__error">
      {{ $t('bind.failure') }}
      <div class="p-bind-failure__tip">{{ $t('bind.failTip') }}</div>
    </div>
    <div class="l-operation__content u-align-c">
      <div class="p-bind-failure__img">
        <div class="p-bind-failure__avatar" :class="{ 'c-solid-avatar': sex === 0, 'c-solid-avatar--boy': sex === 1, 'c-solid-avatar--girl': sex === 2 }">
          <div v-if="figure" class="c-solid-avatar__icon" :style="{ backgroundImage: `url(${ figure.headUrl }` }"></div>
        </div>
        <div class="p-bind-failure__link"></div>
      </div>
    </div>
    <div class="l-operation__footer">
      <div class="l-operation__button o-border-button o-button--cancel" @click="cancel">{{ $t('common.cancel') }}</div>
      <div class="l-operation__button o-button t-adapt-button" @click="retry">{{ $t('bracelet.retry') }}</div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'

export default {
  name: 'bind-failure',
  computed: {
    ...mapState(['runtimmeMode']),
    isStaff () {
      return this.$route.params.mode === 'staff'
    },
    url () {
      return this.isStaff ? 'staff/bracelet/bind' : 'parent/bracelet/bind'
    },
    id () {
      return this.$route.params.id
    },
    figure () {
      return this.$store.getters.getFigure(this.$route.params)
    },
    sex () {
      return this.figure && this.figure.relationship || 0
    }
  },
  methods: {
    cancel () {
      switch (this.runtimmeMode) {
        case 'boot':
          this.$router.replace({ name: 'home' })
          break
        default:
          this.$router.back()
          break
      }
    },
    retry () {
      if (!this.$httpPost) {
        this.$httpPost = this.$http.createPost(this.url, {
          opts: {
            lock: true
          },
          success: () => {
            if (this.figure) {
              this.figure.braceletOfBinding = 1
            }
            this.$router.replace({ name: 'bind-success', params: { id: this.id } })
          }
        })
      }
      this.$httpPost.request(this.isStaff ? {
        token: this.id,
        serialNum: '',
        macAddress: '',
        nfcId: ''
      } : {
        idStudent: this.id,
        serialNum: '',
        macAddress: '',
        nfcId: ''
      })
    }
  },
  beforeDestroy () {
    if (this.$httpPost) {
      this.$httpPost.cancel()
    }
  }
}
</script>

