<template>
  <div class="l-navigation">
    <navigation class="l-navigation__header" :title="$t('home.menu.kids')"></navigation>
    <div class="l-navigation__content has-padding">
      <ul>
        <li v-for="figure in figures" :key="figure.id" class="c-children-item" @click="choose(figure)">
          <div class="c-children-item__avatar" :class="{ 'c-avatar--boy': figure.relationship === 1, 'c-avatar--girl': figure.relationship === 2 }">
            <div class="c-avatar__icon" :style="{ backgroundImage: `url(${ figure.headUrl }` }"></div>
          </div>
          <div class="c-children-item__name u-more">{{ figure.lastName || figure.userName }}</div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import navigation from '@/components/NavigationBar.vue'

export default {
  name: 'children',
  components: {
    navigation
  },
  computed: {
    ...mapState(['figures'])
  },
  methods: {
    choose (figure) {
      this.$router.push({ name: 'children-info', params: { id: figure.studentId } })
    }
  }
}
</script>

<style lang="scss">
.c-children-item {
  position: relative;
  padding: 35px 15px 35px 80px;
  @include border-bottom-1px(#e6e6e6);
  &__avatar {
    position: absolute;
    top: 16px;
    left: 0;
  }
  &__name {
    color: #404040;
    font-size: 16px;
    line-height: 1;
    padding: 7px 0;
    white-space: nowrap;
    overflow: hidden;
  }
}
</style>
