<template>
  <div class="l-operation p-bind">
    <div class="l-operation__title">{{ $t('bind.title') }}</div>
    <div class="l-operation__header">{{ tip }}</div>
    <div class="l-operation__content u-align-c">
      <img class="p-bind__img" src="../../assets/placeholder.png" />
      <div class="p-bind__list">
        <div v-for="(figure, fIndex) in unbindFigures" :key="figure.id" class="p-bind__item p-bind__item--toggle" :class="{ 'is-active': fIndex === index, 'c-solid-avatar--boy': figure.relationship === 1, 'c-solid-avatar--girl': figure.relationship === 2 }" @click="toggle(fIndex)">
          <div class="c-solid-avatar__icon" :style="{ backgroundImage: `url(${ figure.headUrl }` }"></div>
          <div class="p-bind__name">{{ figure.lastName }}</div>
        </div>
      </div>
    </div>
    <div class="l-operation__footer">
      <div class="l-operation__button o-border-button o-button--cancel" @click="cancel">{{ $t('common.cancel') }}</div>
      <div class="l-operation__button o-button t-adapt-button" @click="toBind">{{ $t('bracelet.bind') }}</div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'

export default {
  name: 'bind-choosen',
  data () {
    return {
      index: -1,
      state: 'bind',
    }
  },
  computed: {
    ...mapState(['figures']),
    mode () {
      return this.$route.params.mode
    },
    isStaff () {
      return this.mode === 'staff'
    },
    unbindFigures () {
      return this.figures.filter(figure => figure.braceletOfBinding === 0)
    },
    has () {
      return this.figures.length > 0
    },
    tip () {
      return this.$route.params.type === 'again' ? this.$t('bind.again') : this.$t('bind.tip')
    }
  },
  methods: {
    toggle (index) {
      if (this.index === index) {
        this.index = -1
      } else {
        this.index = index
      }
    },
    cancel () {
      this.$router.replace({ name: 'home' })
    },
    toBind () {
      if (this.index >= 0) {
        this.$router.replace({ name: 'scan', params: { id: this.isStaff ? this.unbindFigures[this.index].token : this.unbindFigures[this.index].studentId } })
      }
    },
    check () {
      if (this.unbindFigures.length === 0) {
        this.cancel()
        return true
      }
      if (this.figures.length === 1) {
        this.index = 0
        this.toBind()
        return true
      }
      return false
    }
  },
  watch: {
    has (val) {
      if (val) {
        this.check()
      }
    }
  },
  created () {
    if (this.has) {
      this.check()
    }
  }
}
</script>

<style lang="scss">
.p-bind {
  &__img {
    display: inline-block;
    width: 325px;
    height: 244px;
  }
  &__list {
    padding: 20px 0 40px;
    white-space: nowrap;
    text-align: center;
    overflow-x: auto;
  }
  &__item {
    & + .p-bind__item {
      margin-left: 50px;
    }
    &--toggle {
      &::after {
        content: '';
        position: absolute;
        bottom: 0;
        right: 0;
        @include bg-img-ratio('../../assets/unchecked', 34, 34);
      }
      &.is-active::after {
        @include bg-img-ratio-normal('../../assets/checked');
      }
    }
  }
  &__name {
    position: relative;
    top: 72px;
    color: #404040;
    font-size: 14px;
    line-height: 1;
  }
}
</style>
