<template>
  <div class="p-home" :class="{ 'has-side': showSide }">
    <side-menu v-once class="p-home__side" :user="user"></side-menu>
    <div class="p-home__main">
      <div class="p-home__shortcut">
        <i class="p-home__menu" @click="showSide = true;"></i>
        <i class="p-home__scan" @click="scan"></i>
      </div>
      <div class="p-home__header swiper-container" id="person-swiper">
        <div class="swiper-wrapper">
          <character-intro v-for="figure in figures" :key="figure.id" class="p-home__person swiper-slide" :mode="mode" :person="figure"></character-intro>
        </div>
        <div class="swiper-pagination" id="person-pagination"></div>
      </div>
      <div v-if="toast" class="p-home__toast p-home-toast" :class="`p-home-toast--${ toast }`" @click="clickToast">
        {{ $t(`home.toast.${ toast }`) }}
        <div class="p-home-toast__icon" @click.stop="clickToastIcon"></div>
      </div>
      <div class="p-home__section">
        <div class="p-home-block">
          <div class="p-home-block__header" @click="toHistory">
            <span class="p-home-block__title">{{ $t('home.timeline.title') }}</span>
            <span class="p-home-block__tip">{{ today }}</span>
            <i v-if="this.records.length > 0" class="p-home-block__arrow"></i>
          </div>
          <div class="p-home-block__content">
            <div class="c-timeline p-home-timeline">
              <div v-for="(point, index) in timeline" :key="index" class="c-timeline__point">
                <div class="c-timeline__header"></div>
                <div class="c-timeline__tail"></div>
                <div class="c-timeline__content" :class="`p-home-timeline__level${ index }`">
                  <div class="c-timeline__time">{{ point.time }}</div>
                  <div class="c-timeline__info">
                    <div class="c-timeline__row is-nowrap">{{ $t(`record.${ point.type }`) }}</div>
                    <div v-if="point.type === 3" class="c-timeline__row is-nowrap">
                      <div class="p-home-timeline__bus t-adapt-color">{{ $t('record.bus') }}</div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <component v-if="isStaff" class="p-home__section" :is="attendanceComponent"></component>
      <div class="p-home__section">
        <div class="p-home-block">
          <div tag="div" class="p-home-block__header" @click="toMotion">
            <span class="p-home-block__title">{{ $t('home.motion.title') }}</span>
            <i class="p-home-block__arrow"></i>
          </div>
          <div class="p-home-block__content">
            <div class="p-home-steps u-clearfix">
              <i class="p-home-steps__icon"></i>
              <div class="p-home-steps__tip">{{ $t('home.motion.tip') }}</div>
              <div class="p-home-steps__value">{{ steps }}<span class="p-home-steps__unit">{{ $t('home.motion.unit') }}</span></div>
            </div>
          </div>
        </div>
      </div>
      <div v-if="showSide" class="p-home__mask" @click="showSide = false;" @touchmove.stop.prevent></div>
    </div>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'
import Swiper from 'swiper'
import sideMenu from '@/views/side-menu/SideMenu.vue'
import characterIntro from '@/views/shared/CharacterIntroHome.vue'

import homeAttendanceAdmin from '@/views/attendance/HomeAttendanceAdmin.vue'
import homeAttendanceTeacher from '@/views/attendance/HomeAttendanceTeacher.vue'

export default {
  name: 'home',
  components: {
    sideMenu,
    characterIntro,
    homeAttendanceAdmin,
    homeAttendanceTeacher
  },
  props: [],
  data () {
    return {
      showSide: false,
      records: [],
      index: 0,
      toast: '',
      date: new Date(),
      steps: 0,
      need: {}
    }
  },
  computed: {
    ...mapState(['user', 'figures']),
    mode () {
      return this.$route.params.mode === 'parent' ? 'parent' : 'staff'
    },
    isStaff () {
      return this.mode === 'staff'
    },
    today () {
      return this.$t('format.date', {
        week: this.$d(this.date, 'week'),
        month: this.$d(this.date, 'month'),
        day: this.date.getDate(),
        year: this.date.getFullYear()
      })
    },
    timeline () {
      let length = this.records.length
      let id = 0
      let array
      switch (true) {
        case length > 3:
          array = this.records.slice(0, 3).map(this.transformRecord)
          break
        case length === 3:
          array = this.records.map(this.transformRecord)
          break
        default:
          array = this.records.map(this.transformRecord)
          for (let i = length; i < 3; i++) {
            array.push(this.records[i] || {
              id: id++,
              type: 0,
              time: '--:--:--'
            })
          }
          break
      }
      return array
    },
    figure () {
      return this.figures.length > 0 ? this.figures[this.index] : null
    },
    id () {
      return this.figure ? this.isStaff ? this.figure.token : this.figure.studentId : null
    },
    attendanceComponent () {
      return this.user.roles === 3 ? 'home-attendance-admin' : 'home-attendance-teacher'
    }
  },
  methods: {
    ...mapActions(['setRuntimeMode']),
    transformRecord (record) {
      return {
        id: record.checkTime,
        type: record.type,
        time: record.checkTimeStr.split(' ')[1]
      }
    },
    checkFlag () {
      if (this.figure) {
        if (!this.need[this.id]) {
          this.need[this.id] = {
            bracelet: true,
            bus: true
          }
        }
        let flag = this.need[this.id]
        if (flag.bracelet && this.figure.braceletOfBinding === 0) {
          this.toast = 'bracelet'
          return
        }
        if (!this.isStaff && flag.bus && !this.figure.busId) {
          this.toast = 'bus'
          return
        }
      }
      if (!jsNative.isBluetoothOpen()) {
        this.toast = 'bluetooth'
        return
      }
      this.toast = ''
    },
    refetch (clear) {
      this.date = new Date()
      this.checkFlag()
      if (clear) {
        this.records = []
        this.steps = 0
        if (this.$recordsHttpPost) {
          this.$recordsHttpPost.cancel()
        }
        if (this.$stepHttpPost) {
          this.$stepHttpPost.cancel()
        }
      }
      if (this.figure) {
        if (!this.$recordsHttpPost) {
          this.$recordsHttpPost = this.$http.createPost(this.isStaff ? 'staff/signinrecord' : 'parent/child/signinrecord', {
            opts: {
              cycle: 60000
            },
            success: res => this.records = res.data
          })
        }
        this.$recordsHttpPost.request(this.isStaff ? {
          token: this.figure.token,
          page: 1,
          pageSize: 3
        } : {
          idStudent: this.figure.studentId,
          page: 1,
          pageSize: 3
        })
        if (!this.$stepHttpPost) {
          this.$stepHttpPost = this.$http.createPost(this.isStaff ? 'staff/sport' : 'parent/child/sport', {
            opts: {
              retry: true
            },
            success: res => this.steps = res.data.steps
          })
        }
        this.$stepHttpPost.request(this.isStaff ? {
          token: this.figure.token
        } : {
          idStudent: this.figure.studentId,
          token: this.user.token
        })
      }
    },
    updatePerson () {
      if (this.figures.length > 1 && !this.$personSwiper) {
        this.$personSwiper = new Swiper('#person-swiper', {
          // direction: 'vertical', // 垂直切换选项
          loop: false, // 循环模式选项
          // 如果需要分页器
          pagination: {
            el: '#person-pagination',
            longSwipesRatio: 0.3
          },
          on: {
            slideChangeTransitionStart: () => {
              this.$router.replace({ name: 'home', params: { index: this.$personSwiper.activeIndex } })
            }
          }
        })
        let index = Math.min(this.index, this.figures.length - 1)
        if (index > 0) {
          this.$personSwiper.slideTo(index, 0, false)
        }
        this.refetch()
      } else {
        this.refetch()
      }
    },
    scan () {
      jsNative.scan()
    },
    clickToast () {
      switch (this.toast) {
        case 'bracelet':
          this.$router.push({ name: 'scan', params: { id: this.id } })
          break
        case 'bus':
          this.$router.push({ name: 'modify-bus', params: { id: this.id } })
          break
        case 'bluetooth':
          this.$router.push({ name: 'bluetooth' })
          break
        default:
          break
      }
    },
    clickToastIcon () {
      switch (this.toast) {
        case 'bracelet':
        case 'bus':
          this.need[this.id][this.toast] = false
          this.checkFlag()
          break
        case 'bluetooth':
          this.clickToast()
          break
        default:
          break
      }
    },
    toHistory () {
      if (this.figure) {
        this.$router.push({ name: 'history', params: { id: this.id } })
      }
    },
    toMotion () {
      if (this.figure) {
        this.$router.push({ name: 'motion', params: { id: this.id } })
      }
    },
    toBind () {
      if (this.figure) {
        this.$router.push({ name: 'scan', params: { id: this.id } })
      }
    },
    toBus () {
      if (this.figure) {
        this.$router.push({ name: 'bus', params: { id: this.id } })
      }
    }
  },
  watch: {
    figures () {
      this.$nextTick(() => {
        this.updatePerson()
      })
    }
  },
  beforeRouteUpdate (to, from, next) {
    let index = to.params.index || 0
    if (index !== this.index) {
      this.index = index
      this.refetch(true)
      if (this.$personSwiper && this.$personSwiper.realIndex !== index) {
        this.$personSwiper.slideTo(index, 0, false)
      }
    }
    next()
  },
  created () {
    this.setRuntimeMode('normal')
    this.index = this.$route.params.index || 0
    this.refetch()
  },
  mounted () {
    this.updatePerson()
  },
  beforeDestroy () {
    if (this.$personSwiper) {
      this.$personSwiper.destroy()
    }
    if (this.$recordsHttpPost) {
      this.$recordsHttpPost.cancel()
    }
    if (this.$stepHttpPost) {
      this.$stepHttpPost.cancel()
    }
  }
}
</script>

<style lang="scss">
.p-home {
  position: relative;
  width: 100%;
  height: 100%;
  overflow-x: hidden;
  &__main {
    position: relative;
    min-height: 100%;
    width: 100%;
    transition: transform 0.2s;
    background-color: #f2f2f2;
    background-size: 100% 262.5px;
    background-repeat: no-repeat;
    overflow: hidden;
  }
  &__shortcut {
    box-sizing: border-box;
    padding: 10px 15px 0;
    height: 50px;
  }
  &__menu {
    display: inline-block;
    @include bg-img-ratio('../../assets/icon_menu', 42, 32);
  }
  &__scan {
    float: right;
    @include bg-img-ratio('../../assets/icon_scan', 42, 42);
  }
  &__header {
    height: 140px;
    margin-bottom: 14px;
    .swiper-container {
      overflow: visible;
    }
    .swiper-wrapper {
      white-space: nowrap;
    }
    .swiper-pagination {
      bottom: 0;
    }
  }
  &__person {
    box-sizing: border-box;
    width: 100%;
    padding: 0 15px;
  }
  &__toast {
    margin: 0 15px 4px;
  }
  &__section {
    padding: 0 15px 12px;
  }
  .p-home-timeline {
    padding: 20px 0 26px;
    &__bus {
      display: inline-block;
      box-sizing: border-box;
      padding: 6px 8px;
      font-size: 12px;
      line-height: 1;
      white-space: nowrap;
      overflow: hidden;
      border-radius: 12px;
    }
    &__level1 {
      color: rgba(#333, 0.7);
    }
    &__level2 {
      color: rgba(#333, 0.3);
    }
  }
  &__mask {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(#333, 0.4);
    z-index: 99;
  }
  &.has-side {
    .p-home__side {
      transform: translateX(0);
    }
    .p-home__main {
      transform: translateX(295px);
    }
  }
  .p-home-steps {
    padding: 15px 0 30px;
  }
}
.p-home-toast {
  position: relative;
  box-sizing: border-box;
  padding: 16px 50px 16px 45px;
  border-radius: 6px;
  color: #333;
  font-size: 12px;
  line-height: 1;
  white-space: nowrap;
  overflow: hidden;
  background-color: #fffaeb;
  &__icon {
    position: absolute;
    top: 0;
    right: 0;
    width: 43px;
    height: 100%;
    &::after {
      content: 'X';
      position: absolute;
      top: 15px;
      right: 15px;
      color: #333;
      font-size: 14px;
      font-weight: bold;
      line-height: 1;
    }
  }
  &::after {
    content: '';
    position: absolute;
    top: 10px;
    right: 43px;
    height: 24px;
    width: 1px;
    background-color: #c1c1c1;
    text-align: center;
  }
  &--bracelet::before {
    content: '';
    position: absolute;
    top: 12px;
    left: 16px;
    @include bg-img-ratio('../../assets/no_bracelet', 39, 37);
  }
  &--bus::before {
    content: '';
    position: absolute;
    top: 12px;
    left: 16px;
    @include bg-img-ratio('../../assets/unbus', 41, 36);
  }
  &--bluetooth {
    &::before {
      content: '';
      position: absolute;
      top: 12px;
      left: 16px;
      @include bg-img-ratio('../../assets/no_bluetooth', 39, 37);
    }
    .p-home-toast__icon::after {
      content: '';
      position: absolute;
      top: 16px;
      right: 15px;
      @include bg-img-ratio('../../assets/more', 14, 24);
    }
  }
}
.p-home-block {
  background-color: #fff;
  padding: 0 15px;
  border-radius: 6px;
  &__header {
    position: relative;
    height: 50px;
    line-height: 48px;
    overflow: hidden;
    @include border-bottom-1px(#e6e6e6);
  }
  &__title {
    display: inline-block;
    color: #333333;
    font-size: 16px;
    font-weight: bold;
    line-height: 1;
  }
  &__tip {
    display: inline-block;
    background-color: #f6f8fb;
    padding: 4px 10px;
    margin-left: 10px;
    color: #a7a8aa;
    font-size: 12px;
    line-height: 1;
  }
  &__supplement {
    display: inline-block;
    margin-left: 2px;
    color: rgba(#333, 0.3);
    font-size: 12px;
    line-height: 1;
  }
  &__content {
    position: relative;
  }
  &__arrow {
    position: absolute;
    top: 50%;
    right: 0;
    margin-top: -6px;
    @include bg-img-ratio('../../assets/more', 14, 24);
  }
}
.p-home-steps {
  &__icon {
    display: inline-block;
    @include bg-img-ratio('../../assets/step', 72, 72);
  }
  &__tip {
    display: inline-block;
    vertical-align: top;
    padding-left: 10px;
    color: rgba(#000, 0.4);
    font-size: 12px;
    line-height: 36px;
  }
  &__value {
    float: right;
    color: #000;
    font-size: 28px;
    line-height: 36px;
  }
  &__unit {
    padding-left: 4px;
    color: rgba(#000, 0.3);
    font-size: 10px;
  }
}
</style>
