<template>
  <div class="l-navigation c-modify">
    <navigation class="l-navigation__header" :title="$t('account.bus')"></navigation>
    <div class="l-navigation__content l-form">
      <form-field type="number" :placeholder="$t('account.bus')" :tip="$t('bus.tip')" v-model.trim="busId">
      </form-field>
      <div class="o-button o-button--submit t-adapt-button--active c-modify__submit" :class="{ 'is-active': isValid }" @click="save">{{ $t('common.save') }}</div>
      <div class="c-modify__error">{{ error }}</div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import navigation from '@/components/NavigationBar.vue'
import formField from '@/components/FormField.vue'

export default {
  name: 'modify-bus',
  components: {
    navigation,
    formField
  },
  data () {
    return {
      busId: '',
      error: ''
    }
  },
  computed: {
    ...mapState(['user']),
    id () {
      return this.$route.params.id
    },
    figure () {
      return this.$store.getters.getFigure({
        mode: 'parent',
        id: this.id
      })
    },
    isValid () {
      return !!this.busId
    }
  },
  methods: {
    save () {
      if (this.isValid) {
        this.error = ''
        if (!this.$httpPost) {
          this.$httpPost = this.$http.createPost('parent/child/modifyBSSId', {
            opts: {
              lock: true
            },
            success: (res, data) => {
              if (this.figure) {
                this.figure.busId = data.BSSId
              }
              this.$router.back()
            },
            fail: error => {
              this.error = error.message
            }
          })
        }
        this.$httpPost.request({
          token: this.user.token,
          idStudent: this.id,
          BSSId: this.busId
        })
      }
    }
  },
  beforeDestroy () {
    if (this.$httpPost) {
      this.$httpPost.cancel()
    }
  }
}
</script>
