<template>
  <div class="l-operation p-bind-success">
    <div class="l-operation__title">{{ $t('bind.title') }}</div>
    <div class="l-operation__header">{{ $t('bind.success') }}</div>
    <div class="l-operation__content u-align-c">
      <div class="p-bind-success__img">
        <div class="p-bind-success__avatar" :class="{ 'c-solid-avatar': sex === 0, 'c-solid-avatar--boy': sex === 1, 'c-solid-avatar--girl': sex === 2 }">
          <div v-if="figure" class="c-solid-avatar__icon" :style="{ backgroundImage: `url(${ figure.headUrl }` }"></div>
        </div>
        <div class="p-bind-success__link"></div>
      </div>
    </div>
    <div class="l-operation__footer">
      <div class="o-button t-adapt-button" @click="next">{{ $t('common.confirm') }}</div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'

export default {
  name: 'bind-success',
  data () {
    return {}
  },
  computed: {
    ...mapState(['figures', 'runtimmeMode']),
    figure () {
      return this.$store.getters.getFigure(this.$route.params)
    },
    sex () {
      return this.figure && this.figure.relationship || 0
    }
  },
  methods: {
    next () {
      switch (this.runtimmeMode) {
        case 'boot':
          if (this.figures.length === 1) {
            this.$router.replace({ name: 'home' })
          } else {
            this.$router.replace({ name: 'bind', params: { type: 'again' } })
          }
          break
        default:
          this.$router.back()
          break
      }
    }
  }
}
</script>

